import Panel from './panel'
import LoginForm from './login'
import react from 'react';
import './App.css'
class App extends react.Component {
  
   state={login:false}
   login=()=>{
      (this.state.login)?this.setState({login:false}):this.setState({login:true})
   }
   componentDidMount()
   {
      // const checkLogin=async ()=>{
      //    let check=await fetch(`/loginCheck`,{ method:'get', headers:{'Content-Type':'application/json'}})
      //    check=await check.json();
      //    return (check)?this.setState({login:true}):this.setState({login:false});
      // }
      // checkLogin();
   }
   static getDerivedStateFromProps(props,state)
   {
      function getCookie(cname) {
         let name = cname + "=";
         let decodedCookie = decodeURIComponent(document.cookie);
         let ca = decodedCookie.split(';');
         for(let i = 0; i <ca.length; i++) {
           let c = ca[i];
           while (c.charAt(0) == ' ') {
             c = c.substring(1);
           }
           if (c.indexOf(name) == 0) {
             return c.substring(name.length, c.length);
           }
         }
         return "";
       }
      return (getCookie("jwt_token"))?{login:true}:null;
   }
 render() {
  return (
     <>
     {(this.state.login)?<Panel login={this.login}/>:<LoginForm login={this.login}/>}
     </>
  )
      }
  
  
}
export default App;
