import React from 'react';
import './login form.css'
import '../node_modules/antd/dist/antd.css';
import {Typography,Input,Button,Checkbox } from 'antd';
import {EyeInvisibleOutlined, EyeTwoTone,LoadingOutlined} from '@ant-design/icons';
import {BsPersonCheck,BsLock,BsFillUnlockFill} from 'react-icons/bs'
const { Title } = Typography;
export default class LoginForm extends React.Component {
    state={login:false}
    Login=async ()=>{
      this.setState({login:true})
      let username=document.getElementById("username").value;
      let pswd=document.getElementById("pswd").value;
      let login=await fetch(`/login`,{ method:'POST', headers:{'Content-Type':'application/json'},body:JSON.stringify({username:username,pswd:pswd})})
      login=await login.json();
     (login.message)?this.props.login():document.querySelector("#login_error").innerHTML='usermame or password incorrect!!'
     this.setState({login:false})
    }
  render() {
    return (
     <>
     <div className='row login-form  m-0 p-0'>
       <div className='col-md-6 p-0 m-0 flex-column d-none d-md-flex'>
          <img src={process.env.PUBLIC_URL + '/nutrifactor.png'} className=''/> 
          <Title level={4} style={{color:'white'}}>Nutrifactor Attendance System</Title>
       </div>
       <div className='col-md-6  p-0 m-0'>
                     <form className='form '>
                     <Title level={5} className='m-0 mb-3' style={{color:'#008269'}}><BsFillUnlockFill className='mr-2'/>Log in to your account</Title>
                     <Input addonAfter={<BsPersonCheck style={{fontSize:'17px'}}/>} placeholder="user name" className='mb-3' id='username'/>
                     <Input.Password placeholder="password" className='mb-3' iconRender={visible => (visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />)}  addonAfter={<BsLock style={{fontSize:'17px'}}/>} id='pswd'/>
                     <Checkbox className='mb-3' style={{color:'#008269'}}>Remember me</Checkbox>
                     {
                       (!this.state.login)?
                       <Button type="primary" block style={{backgroundColor:'#008269',borderColor:'#008269'}} onClick={this.Login}><BsFillUnlockFill className='mr-2'/>Login</Button>:
                       <Button type="primary" block style={{backgroundColor:'#008269',borderColor:'#008269'}}><LoadingOutlined className='mr-1'/>Login</Button>
                     }
                     
                     <small id='login_error' style={{color:"crimson"}} className='mt-3'>-</small>
                     </form>
       </div>
     </div>
     </>
    );
  }
  
}